import React, { Component } from 'react';
import {
    CCol,
    CRow,
} from '@coreui/react'
import {  faArrowLeft, faUndoAlt } from '@fortawesome/free-solid-svg-icons';
import Subcate from "../component/Subcate";
import CateCard from '../component/CateCard'
import ProductList from "../component/ProductList"
import "../../scss/Card.scss";
import { Link } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { Base_URL, imgPath } from "../../CommonRoutes/Api"
import { NavLink } from 'react-router-dom';
import VendorDetail from '../component/VendorDetail';
import BlockUI from 'src/CommonRoutes/BlockUI/BlockUI';


class ViewAllProduct extends Component {
    constructor(props) {
        super(props)
        this.state = {
            cate: true,
            category: [],
            isCity: "",
            cityname: [],
            cityError: "",
            subcate: false,
            subcategory: [],
            vendor: false,
            product: false,
            productlist: [],
            vendorlist: [],
            nearby: false,
            shoplist: [],
            Catid:null, 
            vendordetail:false,
            detail:[],
            blocking:false,
            city:localStorage.getItem("city"),
            


        }
    }

    componentDidMount() {
        this.setState({blocking:true})
        fetch(Base_URL + "allCategories").then((response) => {
            response.json().then((result) => {
                console.log(result.data);
                this.setState({ category: result,blocking:false})
                setTimeout(
                    () => this.setState({ position: 1,blocking:false }), 
                    3000
                  );
            })
        })

    }

    back(){
        this.props.history.push('/dashboard');     
      }

    handleSubcat(id) {
        this.setState({Catid:id})
        this.setState({ cate: false, subcate: true,blocking:true })
        fetch(Base_URL + "getSubCategorybycatid/" + id).then((response) => {
            response.json().then((result) => {
                console.log("sub", result);
                this.setState({ subcategory: result,blocking:false })
                setTimeout(
                    () => this.setState({ position: 1,blocking:false }), 
                    3000
                  );
            })
        })

    }
   
    backcate(){
        this.setState({ cate: true, subcate: false,product:false }) 
    }

    backsub(){
        this.setState({ cate: false, subcate: true,product:false }) 
      }


    handleProduct(id){  
        this.setState({cate:false,subcate:false,product:true,blocking:true})
        fetch(Base_URL + "product_by_cat_subcat/" +this.state.Catid+ "/" +id).then((response) => {
          response.json().then((result) => {
            console.log( "product",result.data);
            this.setState({productlist : result,blocking:false })
            setTimeout(
                () => this.setState({ position: 1,blocking:false }), 
                3000
              );
          })
        })
      
      }


      handleVdetails(id) {
        this.setState({ cate: false, subcate: false, product: false, vendordetail: true,blocking:true })
        fetch(Base_URL + "getvendorbyid/" + id).then((response) => {
            response.json().then((result) => {
                console.log("product", result);
               this.setState({ detail: result,blocking:false })
               setTimeout(
                () => this.setState({ position: 1,blocking:false }), 
                3000
              );
            })
        })

    } 

    backvendor(){
        this.setState({ cate: false, subcate:false, product:true,vendordetail:false})
    }

 


    render() {
        return (
            <div>
                { this.state.cate ?
                    <div>
                        <CRow style={{ margin: "-16px" }}>
                            <CCol md="12" lg="12" xl="12" className="content-center" style={{
                                backgroundColor: "#110730",
                                marginBottom: "0.5rem",
                                
                            }}>
                            
                                  <Link href="#" onClick={()=>this.back()}> <FontAwesomeIcon icon={faArrowLeft} style={{marginLeft:"-5rem",color:"orange",float:"left"}}/>  </Link> 
                                <h3 style={{ color: "orange" }} >All Categories</h3>
                            </CCol>
                        </CRow>
                        <CRow style={{backgroundColor:"white",marginTop: "-0.5rem"}}>
                        {
                            this.state.category.data && this.state.category.data.length > 0 
                        ?
         
                            this.state.category.data.map((item) =>
                            <CCol xs="6" sm="6" md="6">
                                <Link href="#" onClick={() => this.handleSubcat(item.id)}>
                                    <CateCard catename={item.CatEngName}
                                        catid={item.id}
                                        catImg={imgPath+"Category_pics/"+item.CatImage}
                                    />
                                </Link>
                                </CCol>
                            ):'No data Found'}
                            </CRow>
                    </div>
                    : ""}
                { this.state.subcate ?
                    <div>
                        <CRow style={{ margin: "-16px" }}>
                            <CCol md="12" lg="12" xl="12" className="content-center" style={{
                                backgroundColor: "#110730",
                                marginBottom: "0.5rem",
                                color:"orange"
                            }}>
                                 <Link href="#" onClick={()=>this.backcate()}> <FontAwesomeIcon icon={faArrowLeft} style={{marginLeft:"-4rem",color:"orange",float:"left"}}/>  </Link> 
                                <h3 style={{ color: "orange" }} >All SubCategories</h3>
                            </CCol>
                        </CRow>
                        <CRow style={{backgroundColor:"white",marginTop: "-0.5rem"}}>
                        {
                            this.state.subcategory.data && this.state.subcategory.data.length > 0 
                        ?
         
                            this.state.subcategory.data.map((item) =>
                            <CCol xs="6" sm="6" md="6">
                                <Link href="#" onClick={() => this.handleProduct(item.id)}><Subcate subcatename={item.SubCatEngName}
                                    subcatImg={imgPath+"SubCategory_pics/"+item.SubCatImage}
                                />
                                </Link>
                                </CCol>
                        
                            ):'No data Found'}
                            </CRow>
                    </div>
                    : ""}
               
                { this.state.product ?
                    <div>
                        <CRow style={{ margin: "-16px" }}>
                            <CCol md="12" lg="12" xl="12" className="content-center" style={{
                                backgroundColor: "#110730",
                                marginBottom: "0.5rem",
                                color:"orange"
                            }}>
                                  <Link href="#" onClick={()=>this.backsub()}> <FontAwesomeIcon icon={faArrowLeft} style={{marginLeft:"-5rem",color:"orange",float:"left"}}/>  </Link> 
                                <h3 style={{ color: "orange" }} >Our Products</h3>
                            </CCol>
                        </CRow>
                        <CRow style={{backgroundColor:"white",marginTop: "-0.5rem"}}>
                        {
                            this.state.productlist.data && this.state.productlist.data.length > 0 
                        ?
         
                            this.state.productlist.data.map((item) =>
                            <CCol xs="6" sm="6" md="6">
                                <Link href="#" onClick={() => this.handleVdetails(item.vendor_id)}><ProductList name={item.product_name}
                                    productImg={imgPath+"Vendor_Product_images/"+item.product_image}
                                />
                                </Link>
                                </CCol>
                        
                            ):'No data Found'}
                            </CRow>
                    </div>
                    : ""}
                    { this.state.vendordetail ?
                    <div>
                        <CRow style={{ margin: "-16px" }}>
                            <CCol md="12" lg="12" xl="12" className="content-center" style={{
                                backgroundColor: "#110730",
                                marginBottom: "0.5rem"
                            }}>
                              <Link href="#" onClick={()=>this.backvendor()}> <FontAwesomeIcon icon={faArrowLeft} style={{marginLeft:"-4rem",color:"orange",float:"left"}}/>  </Link> 
                                <h3 style={{ color: "orange" }} >{this.state.detail.DisplayName}</h3>
                            </CCol>
                        </CRow>
                        {
                            this.state.detail.data && this.state.detail.data.length > 0 
                        ?
                             <VendorDetail name={this.state.detail.data.CompanyName}
                                 displayName={this.state.detail.data.DisplayName}   
                                 state={this.state.detail.data.State}
                                 city={this.state.detail.data.VendorCity}
                                 address={this.state.detail.data.Address}
                                 mobile={this.state.detail.data.AccountAttachedMobileNo}
                                 pincode={this.state.detail.data.Pincode}
                             />
                               
                            :'No data Found'}   
                    </div>
                    : ""}
                    <BlockUI blocking={this.state.blocking} />
            </div>
        );
    }
}

export default ViewAllProduct;